/* Custom Font Declaration */
@font-face {
  font-family: 'PPEditorialNew-Ultralight';
  src: url('../fonts/PPEditorialNew-Ultralight.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* OT Neue Montreal Font Declarations - Using Extra Squeezed variants */
@font-face {
  font-display: swap;
  font-family: OTNeueMontreal;
  font-style: normal;
  font-weight: 100;
  src: url('../fonts/OTNeueMontreal-ThinExtraSqueezed.otf') format('opentype');
}

@font-face {
  font-display: swap;
  font-family: OTNeueMontreal;
  font-style: normal;
  font-weight: 300;
  src: url('../fonts/OTNeueMontreal-BookExtraSqueezed.otf') format('opentype');
}

@font-face {
  font-display: swap;
  font-family: OTNeueMontreal;
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/OTNeueMontreal-MediumExtraSqueezed.otf') format('opentype');
}

@font-face {
  font-display: swap;
  font-family: OTNeueMontreal;
  font-style: normal;
  font-weight: 500;
  src: url('../fonts/OTNeueMontreal-MediumSemiSqueezed.otf') format('opentype');
}

@font-face {
  font-display: swap;
  font-family: OTNeueMontreal;
  font-style: normal;
  font-weight: 600;
  src: url('../fonts/OTNeueMontreal-SemiBoldExtraSqueezed.otf') format('opentype');
}

@font-face {
  font-display: swap;
  font-family: OTNeueMontreal;
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/OTNeueMontreal-BoldExtraSqueezed.otf') format('opentype');
}

/* Font Variables */

:root {
  --font-size: 16px;
  --background: #ffffff;
  --foreground: oklch(0.145 0 0);
  --card: #ffffff;
  --card-foreground: oklch(0.145 0 0);
  --primary: #030213;
  --primary-foreground: oklch(1 0 0);
  --secondary: oklch(0.95 0.0058 264.53);
  --secondary-foreground: #030213;
  --popover: oklch(1 0 0);
  --popover-foreground: oklch(0.145 0 0);
  --muted: #ececf0;
  --muted-foreground: #717182;
  --accent: #e9ebef;
  --accent-foreground: #030213;
  --destructive: #d4183d;
  --destructive-foreground: #ffffff;
  --input: transparent;
  --switch-background: #cbced4;
  --ring: oklch(0.708 0 0);
  --chart-1: oklch(0.646 0.222 41.116);
  --chart-2: oklch(0.6 0.118 184.704);
  --chart-3: oklch(0.398 0.07 227.392);
  --chart-4: oklch(0.828 0.189 84.429);
  --chart-5: oklch(0.769 0.188 70.08);
  --sidebar: oklch(0.985 0 0);
  --sidebar-foreground: oklch(0.145 0 0);
  --sidebar-primary: #030213;
  --sidebar-primary-foreground: oklch(0.985 0 0);
  --sidebar-accent: oklch(0.97 0 0);
  --sidebar-accent-foreground: oklch(0.205 0 0);
  --sidebar-border: oklch(0.922 0 0);
  --sidebar-ring: oklch(0.708 0 0);
  --border: rgba(0, 0, 0, 0.1);
  --input-background: #f3f3f5;
  --font-weight-medium: 500;
  --font-weight-normal: 400;
  --radius: 0.625rem;
  --radius-sm: calc(var(--radius) - 4px);
  --radius-md: calc(var(--radius) - 2px);
  --radius-lg: var(--radius);
  --radius-xl: calc(var(--radius) + 4px);
  
  /* Typography */

  --text-base: clamp(1rem, 1vw + 0.5rem, 1.125rem);
  --text-lg: clamp(1.25rem, 1.4vw, 1.5rem);
  --text-xl: clamp(1.5rem, 2vw, 2rem);
  --text-2xl: clamp(2rem, 3vw, 3rem);
  --line-height-body: 1.5;
  --line-height-heading: 1.25;
  --max-line-length: 90ch;
}

/* Ensure html and body take up the full height */
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #f1f2e5;
  overflow: hidden;
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  font-size: var(--text-base);
  line-height: var(--line-height-body);
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  touch-action: manipulation;
}

.body {
  background-color: #f1f2e5;
  color: #333;
  min-height: 100vh;
  margin: 0; /* Remove margin */
  padding: 2rem; /* Use padding instead if spacing is needed */
  overflow-x: hidden; /* Prevent horizontal scroll on the body */
  display: flex;
  flex-direction: column;
}

/* Global H1 Style - SemiBold Extra Squeezed */
h1 {
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 600;
  letter-spacing: normal;
  text-transform: uppercase;
}

.pages a {
  font-size: 1rem;
  display: block;
  margin-top: 3rem;
  margin-bottom: 3rem;
  color: #ECE9E4; /* Typical link color */
  text-decoration: none;
  align-items: flex-start;
}





p, li {
  max-width: var(--max-line-length);
  font-size: var(--text-base);
  line-height: var(--line-height-body);
}

h1 {
  font-size: var(--text-2xl);
  line-height: var(--line-height-heading);
  margin: 0 0 0.75rem 0;
}

h2 {
  font-size: var(--text-xl);
  line-height: var(--line-height-heading);
  margin: 0 0 0.65rem 0;
}

h3 {
  font-size: var(--text-lg);
  line-height: var(--line-height-heading);
  margin: 0 0 0.5rem 0;
}

small, .text-small, .chip, .tag {
  font-size: clamp(0.875rem, 0.7vw + 0.75rem, 1rem);
  line-height: var(--line-height-body);
}


