/* Collage Page */
.collage-page {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #f1f2e5;
  z-index: 9000;
  overflow: auto;
}

/* Back Arrow */
.collage-back {
  position: fixed;
  top: 24px;
  left: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
  color: var(--foreground);
  padding: 0.5rem;
  z-index: 9999;
  transition: color 0.3s ease;
}

.collage-back:hover {
  color: #000;
}

/* Collage Canvas */
.collage-canvas {
  position: relative;
  width: 100%;
  min-height: 150vh;
  padding: 80px 20px 100px;
}

/* Block Base */
.collage-block {
  position: absolute;
  cursor: grab;
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  transition: box-shadow 0.3s ease;
  user-select: none;
  max-width: calc(100vw - 40px);
}

.collage-block:hover {
  box-shadow: 0 8px 40px rgba(0,0,0,0.2);
}

.collage-block:active {
  cursor: grabbing;
}

.collage-block.active {
  box-shadow: 0 12px 50px rgba(0,0,0,0.25);
}

/* Resize Handle */
.resize-handle {
  position: absolute;
  bottom: 4px;
  right: 4px;
  width: 20px;
  height: 20px;
  cursor: nwse-resize;
  color: rgba(0,0,0,0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.2s ease;
  z-index: 10;
}

.collage-block:hover .resize-handle,
.collage-block.active .resize-handle {
  opacity: 1;
}

.resize-handle:hover {
  color: rgba(0,0,0,0.5);
}

/* Project Info Card */
.block-info {
  background: var(--card);
  border: 1px solid var(--border);
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  box-shadow: none;
}

.info-title {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-2xl);
  font-weight: 600;
  color: var(--foreground);
  margin: 0;
  text-transform: uppercase;
  line-height: 0.95;
}

.info-subtitle {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--muted-foreground);
  margin: 0;
}

.info-meta {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-top: 0.75rem;
}

.info-role {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--foreground);
}

.info-year {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--muted-foreground);
}

.info-tags {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
  margin-top: 0.75rem;
}

.info-tag {
  border: 1px solid var(--border);
  border-radius: var(--radius-xl);
  padding: 4px 10px;
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: clamp(0.875rem, 0.8vw + 0.75rem, 1rem);
  font-weight: 600;
  color: var(--foreground);
  text-transform: uppercase;
}

/* Hero Block */
.block-hero {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 1.5rem 2rem;
  position: relative;
  overflow: hidden;
}

.hero-content {
  position: relative;
  z-index: 2;
}

.hero-title-wrap,
.hero-subtitle-wrap {
  position: relative;
}

.hero-title {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: clamp(32px, 4vw, 52px);
  font-weight: 600;
  line-height: 0.9;
  letter-spacing: -1.24px;
  color: var(--foreground);
  text-transform: uppercase;
  margin: 0 0 0.5rem 0;
}

.hero-subtitle {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: clamp(32px, 4vw, 52px);
  font-weight: 600;
  line-height: 0.9;
  letter-spacing: -1.24px;
  color: #deff06;
  text-transform: uppercase;
  margin: 0;
}

/* Image Block */
.block-image {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.block-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
}


/* Paper texture effect for image blocks */
.block-image {
  position: relative;
  isolation: isolate;
}

.block-image::before,
.block-image::after {
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
}

/* subtle grain overlay */
.block-image::before {
  background: radial-gradient(circle at 30% 30%, rgba(255,255,255,0.15), transparent 35%),
              radial-gradient(circle at 70% 70%, rgba(0,0,0,0.12), transparent 45%),
              rgba(255,255,255,0.15);
  mix-blend-mode: multiply;
  opacity: 0.55;
}

/* soft inner shadow for paper edges */
.block-image::after {
  box-shadow: inset 0 0 30px rgba(0,0,0,0.12), inset 0 0 60px rgba(0,0,0,0.08);
  opacity: 0.8;
}

.image-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  background: linear-gradient(transparent, rgba(0,0,0,0.6));
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: #fff;
  text-transform: uppercase;
}

/* Text Blocks */
.block-text {
  width: 100%;
  height: 100%;
  padding: 1.25rem 1.5rem;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  background: var(--card);
  box-shadow: none;
}

.block-title {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-lg);
  font-weight: 600;
  text-transform: uppercase;
  margin: 0 0 0.75rem 0;
}

.block-divider {
  height: 1px;
  margin-bottom: 0.75rem;
}

.block-content {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--muted-foreground);
  line-height: 1.5;
  margin: 0;
  flex: 1;
  overflow: auto;
}

.block-list {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--muted-foreground);
  line-height: 1.6;
  margin: 0;
  padding-left: 1.2rem;
  flex: 1;
  overflow: auto;
}

.block-list li {
  margin-bottom: 0.4rem;
}

/* Specific Block Type Styles */
.block-overview {
  background: #f0f5f0;
}
.block-overview .block-title {
  color: #8a9f8a;
}
.block-overview .block-divider {
  background: linear-gradient(90deg, rgba(138,159,138,0.5) 0%, rgba(138,159,138,0) 100%);
}

.block-challenge {
  background: #e8f4f8;
}
.block-challenge .block-title {
  color: #5a8a9c;
}
.block-challenge .block-divider {
  background: linear-gradient(90deg, rgba(90,138,156,0.5) 0%, rgba(90,138,156,0) 100%);
}

.block-solution {
  background: #f5f1ed;
}
.block-solution .block-title {
  color: #b8a080;
}
.block-solution .block-divider {
  background: linear-gradient(90deg, rgba(184,160,128,0.5) 0%, rgba(184,160,128,0) 100%);
}

.block-results {
  background: linear-gradient(135deg, #deff06 0%, #c5e600 100%);
}
.block-results .block-title {
  color: #4a5500;
}
.block-results .block-divider {
  background: linear-gradient(90deg, rgba(74,85,0,0.3) 0%, rgba(74,85,0,0) 100%);
}
.block-results .block-content,
.block-results .block-list {
  color: #3a4200;
}

.block-description {
  background: var(--card);
}
.block-description .block-title {
  color: var(--muted-foreground);
}
.block-description .block-divider {
  background: rgba(151,151,151,0.3);
}

/* Team Section */
.project-team {
  padding: 60px 50px;
  max-width: 1000px;
}

.team-title {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-lg);
  font-weight: 600;
  color: var(--muted-foreground);
  text-transform: uppercase;
  margin: 0 0 1.5rem 0;
}

.team-members {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.team-member {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.member-name {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: 600;
  color: var(--foreground);
}

.member-role {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--muted-foreground);
}

/* Tablet Breakpoint */
@media (max-width: 1024px) {
  .collage-canvas {
    min-height: 180vh;
  }
  
  .block-info {
  background: #f1f2e5;
  border: 1px solid var(--border);
    padding: 1.25rem;
  }
  
  .block-text {
    padding: 1rem 1.25rem;
  }
  
  .block-hero {
    padding: 1.25rem 1.5rem;
  }
}

/* Small Desktop / Large Tablet */
@media (max-width: 900px) {
  .collage-canvas {
    min-height: 200vh;
  }
}

/* Mobile - Disable drag/resize, stack vertically */
@media (max-width: 768px) {
  .collage-canvas {
    min-height: auto;
    padding: 80px 18px calc(80px + env(safe-area-inset-bottom, 0px));
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .collage-block {
    position: absolute;
    min-height: 280px;
    max-width: calc(100vw - 36px);
    cursor: grab;
    touch-action: none;
  }
  
  .collage-block:active {
    cursor: grabbing;
  }
  
  /* Hide resize handle on mobile */
  .resize-handle {
    display: none;
  }
  
  .block-info {
  background: #f1f2e5;
  border: 1px solid var(--border);
    min-height: 300px;
    padding: 1.5rem;
  }
  
  .info-title {
    font-size: 36px;
  }
  
  .info-subtitle {
    font-size: 16px;
  }
  
  .info-role,
  .info-year {
    font-size: var(--text-base);
  }
  
  .info-tag {
    font-size: 10px;
    padding: 5px 10px;
  }
  
  .block-hero {
    min-height: 280px;
  }
  
  .hero-title,
  .hero-subtitle {
    font-size: 32px;
  }
  
  .block-image {
    min-height: 250px;
  }
  
  .block-text {
    min-height: 200px;
    height: auto !important;
    padding: 1.25rem;
  }
  
  .block-title {
    font-size: 18px;
  }
  
  .block-content,
  .block-list {
    font-size: var(--text-base);
  }
  
  .collage-back {
    top: 1rem;
    left: 18px;
  
  }
  .collage-home-logo {
    top: 1rem;
    right: 18px;
  }
  
  .collage-logo-letters {
    display: none;
  }
  
  .collage-logo-title {
    font-size: 0.75rem;
  }
  
  .image-caption {
    font-size: var(--text-base);
  }
  
  .project-team {
    padding: 40px 18px 100px;
  }
  
  .team-members {
    grid-template-columns: 1fr;
  }
  
  .team-title {
    font-size: 18px;
  }
  
  .member-name {
    font-size: var(--text-base);
  }
  
  .member-role {
    font-size: 12px;
  }
}

/* Small Mobile */
@media (max-width: 400px) {
  .block-info {
  background: #f1f2e5;
  border: 1px solid var(--border);
    min-height: 260px;
    padding: 1.25rem;
  }
  
  .info-title {
    font-size: 28px;
  }
  
  .info-subtitle {
    font-size: var(--text-base);
  }
  
  .hero-title,
  .hero-subtitle {
    font-size: 26px;
  }
  
  .block-text {
    padding: 1rem;
  }
  
  .block-title {
    font-size: 16px;
  }
  
  .block-content,
  .block-list {
    font-size: var(--text-base);
  }
}

/* Home Logo */
.collage-home-logo {
  position: fixed;
  top: 24px;
  right: 24px;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  text-decoration: none;
  cursor: pointer;
  z-index: 9999;
  transition: transform 0.3s ease;
}

.collage-home-logo:hover {
  transform: scale(1.02);
}

.collage-logo-letters {
  display: flex;
  flex-direction: column;
  line-height: 0.85;
}

.collage-logo-a {
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  color: #D9D9D9;
  letter-spacing: normal;
}

.collage-logo-m {
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  color: #deff06;
  letter-spacing: normal;
}

.collage-logo-title {
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  font-size: 0.85rem;
  font-weight: 600;
  color: #D9D9D9;
  letter-spacing: normal;
  white-space: nowrap;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .collage-logo-a,
  .collage-logo-m {
    font-size: 1.75rem;
  }
  
  .collage-logo-title {
    font-size: 1.25rem;
  }
}

