  background: #e8f4f8;
}
.block-challenge .block-title {
  color: #5a8a9c;
}
.block-challenge .block-divider {
  background: linear-gradient(90deg, rgba(90,138,156,0.5) 0%, rgba(90,138,156,0) 100%);
}

.block-solution {
  background: #f5f1ed;
}
.block-solution .block-title {
  color: #b8a080;
}
.block-solution .block-divider {
  background: linear-gradient(90deg, rgba(184,160,128,0.5) 0%, rgba(184,160,128,0) 100%);
}

.block-results {
  background: linear-gradient(135deg, #deff06 0%, #c5e600 100%);
}
.block-results .block-title {
  color: #4a5500;
}
.block-results .block-divider {
  background: linear-gradient(90deg, rgba(74,85,0,0.3) 0%, rgba(74,85,0,0) 100%);
}
.block-results .block-content,
.block-results .block-list {
  color: #3a4200;
}

.block-description {
  background: var(--card);
}
.block-description .block-title {
  color: var(--muted-foreground);
}
.block-description .block-divider {
  background: rgba(151,151,151,0.3);
}

/* Team Section */
.project-team {
  padding: 60px 50px;
  max-width: 1000px;
}

.team-title {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-lg);
  font-weight: 600;
  color: var(--muted-foreground);
  text-transform: uppercase;
  margin: 0 0 1.5rem 0;
}

.team-members {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.team-member {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.member-name {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: 600;
  color: var(--foreground);
}

.member-role {
  font-family: OTNeueMontreal, -apple-system, sans-serif;
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--muted-foreground);
}

/* Tablet Breakpoint */
@media (max-width: 1024px) {
  .collage-canvas {
    min-height: 180vh;
  }
  
  .block-info {
  background: #f1f2e5;
  border: 1px solid var(--border);
    padding: 1.25rem;
  }
  
  .block-text {
    padding: 1rem 1.25rem;
  }
  
  .block-hero {
    padding: 1.25rem 1.5rem;
  }
}

/* Small Desktop / Large Tablet */
@media (max-width: 900px) {
  .collage-canvas {
    min-height: 200vh;
  }
}

/* Mobile - Disable drag/resize, stack vertically */
@media (max-width: 768px) {
  .collage-canvas {
    min-height: auto;
    padding: 80px 18px calc(80px + env(safe-area-inset-bottom, 0px));
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .collage-block {
    position: absolute;
    min-height: 280px;
    max-width: calc(100vw - 36px);
    cursor: grab;
    touch-action: none;
  }
  
  .collage-block:active {
    cursor: grabbing;
  }
  
  /* Hide resize handle on mobile */
  .resize-handle {
    display: none;
  }
  
  .block-info {
  background: #f1f2e5;
  border: 1px solid var(--border);
    min-height: 300px;
    padding: 1.5rem;
  }
  
  .info-title {
    font-size: 36px;
  }
  
  .info-subtitle {
    font-size: 16px;
  }
  
  .info-role,
  .info-year {
    font-size: var(--text-base);
  }
  
  .info-tag {
    font-size: 10px;
    padding: 5px 10px;
  }
  
  .block-hero {
    min-height: 280px;
  }
  
  .hero-title,
  .hero-subtitle {
    font-size: 32px;
  }
  
  .block-image {
    min-height: 250px;
  }
  
  .block-text {
    min-height: 200px;
    height: auto !important;
    padding: 1.25rem;
  }
  
  .block-title {
    font-size: 18px;
  }
  
  .block-content,
  .block-list {
    font-size: var(--text-base);
  }
  
  .collage-back {
    top: 1rem;
    left: 18px;
  
  }
  .collage-home-logo {
    top: 1rem;
    right: 18px;
  }
  
  .collage-logo-letters {
    display: none;
  }
  
  .collage-logo-title {
    font-size: 0.75rem;
  }
  
  .image-caption {
    font-size: var(--text-base);
  }
  
  .project-team {
    padding: 40px 18px 100px;
  }
  
  .team-members {
    grid-template-columns: 1fr;
  }
  
  .team-title {
    font-size: 18px;
  }
  
  .member-name {
    font-size: var(--text-base);
  }
  
  .member-role {
    font-size: 12px;
  }
}

/* Small Mobile */
@media (max-width: 400px) {
  .block-info {
  background: #f1f2e5;
  border: 1px solid var(--border);
    min-height: 260px;
    padding: 1.25rem;
  }
  
  .info-title {
    font-size: 28px;
  }
  
  .info-subtitle {
    font-size: var(--text-base);
  }
  
  .hero-title,
  .hero-subtitle {
    font-size: 26px;
  }
  
  .block-text {
    padding: 1rem;
  }
  
  .block-title {
    font-size: 16px;
  }
  
  .block-content,
  .block-list {
    font-size: var(--text-base);
  }
}

/* Home Logo */
.collage-home-logo {
  position: fixed;
  top: 24px;
  right: 24px;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  text-decoration: none;
  cursor: pointer;
  z-index: 9999;
  transition: transform 0.3s ease;
}

.collage-home-logo:hover {
  transform: scale(1.02);
}

.collage-logo-letters {
  display: flex;
  flex-direction: column;
  line-height: 0.85;
}

.collage-logo-a {
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  color: #D9D9D9;
  letter-spacing: normal;
}

.collage-logo-m {
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  color: #deff06;
  letter-spacing: normal;
}

.collage-logo-title {
  font-family: OTNeueMontreal, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  font-size: 0.85rem;
  font-weight: 600;
  color: #D9D9D9;
  letter-spacing: normal;
  white-space: nowrap;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .collage-logo-a,
  .collage-logo-m {
    font-size: 1.75rem;
  }
  
  .collage-logo-title {
    font-size: 1.25rem;
  }
}

@media (min-width: 1024px) {
  .collage-logo-title {
    font-size: 1.75rem;
  }
}

